import React from 'react'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
import HeaderGeneric from '../components/HeaderGeneric'
import pic01 from '../assets/images/pic01.jpg'

class About extends React.Component {
  render() {

    return (
      <Layout>
        <Helmet title="عن المؤسسة" />
        <HeaderGeneric />
        <div id="main">
          <section id="content" className="main special">
            <span className="image main"><img src={pic01} alt="" /></span>
            <header className="major">
              <h1>عن المؤسسة</h1>
            </header>
            <span className="icon style2 fa-bullhorn"></span>
            <h2>رسالة المؤسسة</h2>
            <p>نحن مؤسسة شباب بیحب الخیر للمساعدات و الخدمات و التنمیة,و دعم المجتمعات اللاجئة و المضیفة, نسعى من خلال
كوادرنا البشریة من ضمان وصول اللاجئین و الساعین للجوء من الوصول للعدالة و الحیاة الكریمة و أن یتم توفیر ابسط
سبل المعیشة و الحقوق في ظل النظام القانوني المصري, وتنمیة المجتمع المضیف كشریك ومساعد أساسي لجهود الدولة
المصریة</p>
            <span className="icon style2 fa-eye"></span>
            <h2>رؤیة المؤسسة</h2>
            <p>نحو مجتمع قائم على المساواة و تقبل الأخر, یتمتع فیه الأفراد والمجتمعات المحلیة والفئات المهمشة بجمیع حقوقهم الإنسانیة في المشاركة،
والحیاة النوعیة الكریمة، والعدالة الاجتماعیة، والتنمیة المستدامة</p>
            <span className="icon style2 fa-list"></span>
            <h2>المبادئ</h2>
            <p>تلتزم المؤسسة في برامجها وشركاتها باحترام حقوق الإنسان، وقیم التضامن وحریة التفكیر والرأي والتعبیر، وقبول
التعددیة، والمساواة الجندریة، مع التأكید على تعزیز مكانة المرأة ودورها. تُعلي المؤسسة من قیمة مشاركة الناس في تحدید حاجاتهم وتلبیتها، خاصة بین الفئات صاحبة الحاجات الأساسیة،
وخلق نماذج تغییریة ذات جدوى یمكن تكرارها و مراكمة آثارها ونشرها. تعتبر المؤسسة نهج التشبیك أساسا لتفعیل قیم الشراكة والتعاون، ّ وضم القدرات وترشید استخدام الموارد، والبناء
على قدرات الناس وخبراتهم، في إطار المعرفة العلمیة والتنمیة العادلة. تسعى المؤسسة إلى الإسهام مع شركائها في بناء مجتمعات تؤمن بالعدالة الاجتماعیة، والتضامن، والمساواة.</p>
            <span className="icon style2 fa-check"></span>
            <h2>سیاسة الحمایة</h2>
            <p>كمؤسسة هدفها الأساسي هو مساعدة الأشخاص الأكثر ضعفًا من المجتمعات اللاجئة و المضیفة، ندرك أهمیة
الحمایة وتلتزم بحمایة الموظفین والشركاء والمجتمعات الضعیفة وجمیع أولئك الذین یتعاملون مع عملنا من كل أشكال
الاستغلال سواء كان عاطفیا أو جسدیا, او أعتداء علي المساحة الشخصیة و التحرش بجمیع أشكاله</p>
          </section>
        </div>
      </Layout>
    )
  }
}

export default About
